import React from 'react'
import styles from './styles.module.css'
import { Checkbox } from '@mui/material'
const CoinModalFooter = () => {
  return (
    <div className={styles.wrap}>
        <div className={styles.filter}>
            Фильтр 1
            <Checkbox  />
        </div>
        <div className={styles.filter}>
            Фильтр 2
            <Checkbox  />
        </div>
        <div className={styles.filter}>
            Фильтр 3
            <Checkbox  />
        </div>
        <div className={styles.filter}>
            Фильтр 4
            <Checkbox  />
        </div>
        <div className={styles.filter}>
            Фильтр 5
            <Checkbox  />
        </div>

    </div>
  )
}

export default CoinModalFooter